/* The container must be positioned relative: */
.custom-select {
	position: relative;
}

.custom-select select {
	display: none;
	/*hide original SELECT element: */
}

.select-selected {
	width: 100%;
	height: rem(40);
	border-radius: rem(5);
	color: var(--c-text);
	padding: 0 rem(12);
	border: rem(1) solid #9A9EBC;

	@include placeholder {
		color: var(--c-text);
	}
}

/* Style the arrow inside the select element: */
.select-selected:after {
	position: absolute;
	content: "\e900";
	font-family: 'icons';
	top: rem(16);
	right: rem(14);
	font-size: rem(8);
	color: black;
}

// /* Point the arrow upwards when the select box is open (active): */
// .select-selected.select-arrow-active:after {
// 	border-color: transparent transparent #fff transparent;
// 	top: 7px;
// }

/* style the items (options), including the selected item: */
.select-selected {
	cursor: pointer;
	width: 100%;
	height: rem(40);
	border-radius: rem(5);
	color: var(--c-text);
	background-color: transparent;
	padding: 0 rem(12);
	border: rem(1) solid #B6B6B6;

	display: flex;
	align-items: center;

	font-size: rem(16);
	font-family: var(--f-default);

	@include placeholder {
		color: #2B2B2B;
	}

	@include min768 {
		font-size: rem(14);
	}
}

.select-arrow-active {
	border-bottom-right-radius: 0;
	border-bottom-left-radius: 0;
}

/* Style items (options): */
.select-items {
	position: absolute;
	background-color: white;
	// border-radius: rem(8);
	// height: rem(40);
	top: 100%;
	left: 0;
	right: 0;
	z-index: 99;
	// margin-top: rem(8);

	div {
		cursor: pointer;
		width: 100%;
		height: rem(40);
		// border-radius: rem(5);
		color: var(--c-text);
		background-color: white;
		padding: 0 rem(12);
		border-right: rem(1) solid #BABFC3;
		border-left: rem(1) solid #BABFC3;
		border-bottom: rem(1) solid #BABFC3;

		display: flex;
		align-items: center;

		font-size: rem(16);
		font-family: var(--f-default);

		@include min768 {
			font-size: rem(14);
		}
	}
}

/* Hide the items when the select box is closed: */
.select-hide {
	display: none;
}

.select-items div:hover,
.same-as-selected {
	background-color: rgba(0, 0, 0, 0.1);
}